import { list } from '../../constants/list'
import { Col, Layout } from 'antd'

const { Content } = Layout

const renderList = list => list.map(l => (
    <li key={l.id}>
        <Col span={2} className='name'>
            {l.name}
        </Col>
        <Col span={2}></Col>
        <Col span={20} className='detail'>
            阿水淀粉撒掉色粉色日光灯管
        </Col>
    </li>
))  

const List = lists => {
    const renderLists = renderList(list)

    return (
        <Content>
            <div className='list-box'>
                <ul className='list-detail'>
                    {renderLists}
                </ul>
            </div>
        </Content>
    )
}

export default List